.mob-menu {
  position: absolute;
  top: 50%;
  left: 15px;
  display: none;
  width: 24px;
  height: 24px;
  transform: translateY(-50%);
}

@media screen and (max-width: 1024px) {
  .mob-menu {
    display: inline;
  }
}

.mob-menu .hamburger__menu,
.mob-menu .hamburger__menu::after,
.mob-menu .hamburger__menu::before {
  width: 24px;
  height: 2px;
  border-radius: 2px;
}

.mob-menu .hamburger__menu {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0ms 300ms;
  background-color: #c4c4c4;
}

.mob-menu .hamburger__menu::before {
  content: '';
  position: absolute;
  bottom: 8px;
  left: 0;
  transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
  background: #c4c4c4;
}

.mob-menu .hamburger__menu::after {
  content: '';
  position: absolute;
  top: 8px;
  left: 0;
  transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
  background: #c4c4c4;
}

.mob-menu .hamburger__menu.animate {
  background: rgba(255, 255, 255, 0);
}

.mob-menu .hamburger__menu.animate::after {
  top: 0;
  transform: rotate(45deg);
  transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.mob-menu .hamburger__menu.animate::before {
  bottom: 0;
  transform: rotate(-45deg);
  transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.mob-menu .hamburger__menu.hamburger__menu_white {
  background-color: #fff;
}

.mob-menu .hamburger__menu.hamburger__menu_white::before {
  background: #fff;
}

.mob-menu .hamburger__menu.hamburger__menu_white::after {
  background: #fff;
}

.mob-menu .hamburger__menu.hamburger__menu_white.animate {
  background: rgba(255, 255, 255, 0);
}

