.cabinet p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 16px;
  line-height: 150%;
}

.cabinet .field {
  position: relative;
  margin-bottom: 30px;
}

.cabinet .field.on {
  display: block;
}

.cabinet .field label {
  display: block;
  margin-bottom: 5px;
  color: rgba(179, 135, 100, 0.5);
  font-size: 14px;
  line-height: 16px;
}

.cabinet .field input {
  width: 100%;
  border: 1px solid rgba(179, 135, 100, 0.5);
  background: #0d0e10;
}

.cabinet .field input[type='password'] {
  color: rgba(179, 135, 100, 0.5);
}

.cabinet .field-text {
  position: relative;
}

.cabinet .field-textarea {
  position: relative;
}

.cabinet .field-textarea textarea {
  width: 100%;
  height: 200px;
  border: 1px solid rgba(179, 135, 100, 0.5);
  background: #0d0e10;
  color: rgba(179, 135, 100, 0.5);
  font-size: 14px;
  line-height: 16px;
}

.cabinet .field-textarea textarea:-ms-input-placeholder {
  color: rgba(179, 135, 100, 0.5);
  font-size: 14px;
  line-height: 16px;
}

.cabinet .field-textarea textarea:-moz-placeholder {
  color: rgba(179, 135, 100, 0.5);
  font-size: 14px;
  line-height: 16px;
}

.cabinet .field-textarea textarea::-moz-placeholder {
  color: rgba(179, 135, 100, 0.5);
  font-size: 14px;
  line-height: 16px;
}

.cabinet .field-textarea textarea::-webkit-input-placeholder {
  color: rgba(179, 135, 100, 0.5);
  font-size: 14px;
  line-height: 16px;
}

.cabinet .field-textarea textarea::placeholder {
  color: rgba(179, 135, 100, 0.5);
  font-size: 14px;
  line-height: 16px;
}

.cabinet .field-select {
  position: relative;
  width: 100%;
  background: #232324;
}

.cabinet .field-select:hover .field-select-choices {
  display: block;
}

.cabinet .field-select.select::before {
  content: '';
  position: absolute;
  top: 50%;
  right: 20px;
  width: 16px;
  height: 16px;
  transform: translateY(-50%);
  background-image: url("/assets/files/icons/svg/arrow_bottom_accent.svg");
}

.cabinet .field-select-choices {
  position: absolute;
  z-index: 10;
  display: none;
  width: 100%;
  background-color: #232324;
}

.cabinet .field-select-search {
  position: relative;
}

.cabinet .field-select-search input {
  width: 100%;
  background-color: #232324;
}

.cabinet .field-select-wrap {
  max-height: 320px;
  overflow-y: auto;
}

.cabinet .field-select-choice {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  padding-left: 5px;
  border: 1px solid rgba(179, 135, 100, 0.5);
  border-radius: 2px;
  background: #0d0e10;
  color: rgba(179, 135, 100, 0.5);
  font-size: 14px;
  line-height: 16px;
}

.cabinet .field-select-checked {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  padding-left: 10px;
  border: 1px solid rgba(179, 135, 100, 0.5);
  border-radius: 2px;
  background: #0d0e10;
  color: rgba(179, 135, 100, 0.5);
  font-size: 14px;
  line-height: 16px;
}

.cabinet .field-select-checked::before {
  content: '';
  position: absolute;
  top: 50%;
  right: 10px;
  width: 16px;
  height: 16px;
  transform: translateY(-50%);
  background-image: url("/assets/files/icons/svg/arrow_bottom_accent.svg");
}

.cabinet .field-select-option {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 12px 10px;
  border-top: 1px solid rgba(179, 135, 100, 0.1);
  color: #b38764;
  font-size: 14px;
  line-height: 16px;
}

.cabinet .field-select-option-choice {
  display: flex;
  align-items: center;
  align-self: center;
  margin-right: 5px;
  padding: 3px 7px 5px;
  background: rgba(179, 135, 100, 0.5);
  color: rgba(255, 255, 255, 0.7);
  font-size: 14px;
  line-height: 16px;
}

.cabinet .field-select-option-choice-inline {
  position: relative;
}

.cabinet .field-select-option-choice-inline input {
  height: 14px;
  margin-top: 9px;
  border: none;
  background: transparent;
}

.cabinet .field-select-option-choice span {
  width: 12px;
  height: 12px;
  margin-left: 10px;
  background-image: url("/assets/files/icons/svg/close-gray.svg");
  background-size: cover;
  cursor: pointer;
}

.cabinet .field-checkbox {
  position: relative;
  width: 16px;
  height: 16px;
  margin-right: 10px;
}

.cabinet .field-checkbox label {
  position: absolute;
  top: 0;
  left: 0;
  width: 16px;
  height: 16px;
  border: 1px solid #b38764;
  background: #232324;
  cursor: pointer;
}

.cabinet .field-checkbox label::after {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  opacity: 0;
  width: 12px;
  height: 6px;
  transform: rotate(-45deg);
  border: 1px solid #fff;
  border-top: none;
  border-right: none;
  background: transparent;
}

.cabinet .field-checkbox label:hover::after {
  opacity: 0.3;
}

.cabinet .field-checkbox input[type='checkbox'] {
  visibility: hidden;
}

.cabinet .field-checkbox input[type='checkbox']:checked + label::after {
  opacity: 1;
}

.cabinet .field-show {
  display: flex;
  align-items: center;
  width: 100%;
  margin-top: 7px;
  color: rgba(179, 135, 100, 0.5);
  font-size: 14px;
  line-height: 16px;
  cursor: pointer;
}

.cabinet .field-show img {
  width: 24px;
  height: 24px;
  margin-right: 10px;
}
